<script>
import Menu from '../components/menu'
import Carousel from '../components/carousel'

export default {
  name: 'Home',
  components: { 
    Menu,
    Carousel,
  },
  data () {
    return {
        display: false,
        drawerWidth: '300px',
        langEn:'en',
        langCn:'cn',
        langKo:'ko',
        ishow:false,
    }       
  },
    created(){
        this.$i18n.locale = this.langEn
        localStorage.setItem("changelang",this.langEn)
    },

  mounted(){
    window.addEventListener('scroll', this.handleScroll, true)
  },
  destroyed(){
    window.removeEventListener('scroll', this.handleScroll)
 },
  
  methods: {
    
    switchLangEn(){
        this.$i18n.locale = this.langEn
        localStorage.setItem("changelang",this.langEn)
    },
     switchLangCn(){
        this.$i18n.locale = this.langCn
        localStorage.setItem("changelang",this.langCn)
    },
     switchLangKo(){
        this.$i18n.locale = this.langKo
        localStorage.setItem("changelang",this.langKo)
    },

    joinnow(){
       if(localStorage.getItem('changelang') == 'en'){
          window.open('https://docsify.incoin.fun/#/en/join',"_blank")
       }else if(localStorage.getItem('changelang') == 'cn'){
          window.open('https://docsify.incoin.fun/#/zh/join',"_blank")
       }else{
          window.open('https://docsify.incoin.fun/#/kr/join',"_blank")
       }
    },

    novice(){
        if(localStorage.getItem('changelang') == 'en'){
          window.open('https://docsify.incoin.fun/#/en/Novice/',"_blank")
       }else if(localStorage.getItem('changelang') == 'cn'){
          window.open('https://docsify.incoin.fun/#/zh/Novice/',"_blank")
       }else{
          window.open('https://docsify.incoin.fun/#/kr/Novice/',"_blank")
       }
     },

      convention(){
       if(localStorage.getItem('changelang') == 'en'){
          window.open('https://docsify.incoin.fun/#/en/',"_blank")
       }else if(localStorage.getItem('changelang') == 'cn'){
          window.open('https://docsify.incoin.fun/#/zh/',"_blank")
       }else{
          window.open('https://docsify.incoin.fun/#/kr/',"_blank")
       }
     },

   //返回顶部
   toTop(){
      let top = document.documentElement.scrollTop || document.body.scrollTop
      const timer = setInterval(() => {
       document.body.scrollTop = document.documentElement.scrollTop = top -= 100
        if (top <= 0) {
           clearInterval(timer)
           this.ishow = false
           }
          }, 10)
      },
    handleScroll() {
      let top = document.documentElement.scrollTop || document.body.scrollTop
      if ( top > 300) {
        this.ishow = true
      } else {
        this.ishow = false
      }
    },

     jointg(){
          window.open('https://t.me/inCoinGroup',"_blank")
     },

       whitepaper(){
         if(localStorage.getItem('changelang') == 'en'){
          window.open('https://docsify.incoin.fun/#/en/Scrolls/',"_blank")
       }else if(localStorage.getItem('changelang') == 'cn'){
          window.open('https://docsify.incoin.fun/#/zh/Scrolls/',"_blank")
       }else{
          window.open('https://docsify.incoin.fun/#/kr/Scrolls/',"_blank")
       }
     },

      toswap(){
          window.open('https://exchange.pancakeswap.finance/#/swap?outputCurrency=0xB9c350309473B401861F5a27C0fcCFBEBef04363',"_blank")
      },

     realtime(){
        window.open('https://bscscan.com/address/0xB9c350309473B401861F5a27C0fcCFBEBef04363',"_blank")
     }
  }
}
</script>

